		method: 'GET',
		data: {},
		success: res => {
			console.log(res.data);
			this.title=res.data.title
			this.strings=res.data.content
			this.created_at=res.data.created_at
			this.author_name=res.data.author_name
		},
		fail: () => {},
		complete: () => {}
	});
}
